import { _decorator, Component, Node, v2, Quat, v3 } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('missile')
export class missile extends Component {
    start() {
        let node = this.node.position;
        let nodeX = node.x;
        let nodeY = node.y;
        this.schedule(() => {
            let birY = this.node.parent.getChildByName("enemy").position.y;
            let birX = this.node.parent.getChildByName("enemy").position.x;
            if (nodeY < birY) {
                this.node.setPosition(node.x, node.y + 1);
            } else if (nodeY == birY) {
                this.node.setPosition(node.x, node.y);
            }
            if (birX < 0) {
                if (nodeX > birX) {
                    this.node.setPosition(node.x - 1, node.y);
                } else {
                    this.node.setPosition(node.x, node.y);
                }
            } else if (birX > 0) {
                if (nodeX < birX) {
                    this.node.setPosition(node.x + 1, node.y);
                } else {
                    this.node.setPosition(node.x, node.y);
                }
            }
        }, 0.01);
    }

    update(deltaTime: number) {
        this.rotation();


    }
    //旋转
    rotation() {
        let theangle = Math.atan2(this.node.position.y - this.node.parent.getChildByName("enemy").position.y,
            this.node.position.x - this.node.parent.getChildByName("enemy").position.x);
        let theta = theangle * 180 / Math.PI;
        this.node.eulerAngles = v3(0, 0, theta);
    }
}

